<template>
  <div class="b-textarea-box">
    <a-textarea
      :disabled="disabled"
      v-model="newValue"
      :placeholder="placeholder"
      :auto-size="autoSize"
    />
    <div
      class="tip"
      :style="{
        color: newValue.length > maxLength ? 'red' : '',
      }"
    >
      {{ newValue.length + " / " + maxLength }}
    </div>
  </div>
</template>

<script>
export default {
  model: {
    prop: 'oldValue',
    event: 'change'
  },
  watch: {
    oldValue: {
      handler (val) {
        this.newValue = val
      },
      immediate: true
    },
    newValue: {
      handler (val) {
        this.$emit('change', val)
      }
    }
  },
  data () {
    return {
      newValue: ''
    }
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    autoSize: {
      type: Object,
      default: () => {
        return { minRows: 3, maxRows: 5 }
      }
    },
    placeholder: {
      type: String,
      default: '请输入备注'
    },
    maxLength: {
      type: [Number, String],
      default: 128
    },
    oldValue: {
      type: String,
      default: ''
    }
  }
}
</script>

<style lang="scss" scoped>
.b-textarea-box {
  position: relative;

  .tip {
    position: absolute;
    right: 5px;
    bottom: 5px;
    z-index: 1;
    line-height: 1rem;
    padding: 5px 10px;
    background-image: radial-gradient(#fff, transparent);
  }
}
</style>



